.b-header {
	z-index: 100 !important; }


#fancybox-thumbs {
	position: fixed;
	left: 0;
	width: 100%;
	overflow: hidden;
	z-index: 8050; }


#fancybox-thumbs.bottom {
	bottom: 2px; }


#fancybox-thumbs.top {
	top: 2px; }


#fancybox-thumbs ul {
	position: relative;
	list-style: none;
	margin: 0;
	padding: 0; }


#fancybox-thumbs ul li {
	float: left;
	padding: 1px;
	opacity: 0.5; }


#fancybox-thumbs ul li.active {
	opacity: 0.75;
	padding: 0;
	border: 1px solid #fff; }


#fancybox-thumbs ul li:hover {
	opacity: 1; }


#fancybox-thumbs ul li a {
	display: block;
	position: relative;
	overflow: hidden;
	border: 1px solid #222;
	background: #111;
	outline: none; }


#fancybox-thumbs ul li img {
	display: block;
	position: relative;
	border: 0;
	padding: 0;
	max-width: none; }


.vacancy__item {
	display: flex;

	&-img {
		width: 300px;
		flex: none;
		margin-right: 30px; }

	&-info {
		width: 100%; }

	.vacancy-element {
		display: flex; }

	.element-title {
		max-width: 200px;
		width: 100%; }

	.vacancy-details {
		&[style*="display: block"] {

			~ .js-show-detail-vacancy {
				display: none; } } } }



.b-vacancy {
	.vacancy-element {
		.element-title {
			font-weight: 700;
			flex: none; } } }


.b-container {
	&__top {
		&-title {

			h1 {
				text-align: left; } } }

	&__info {
		display: flex;
		padding: 0 10px;

		&-img {
			flex: none;
			margin-right: 20px;

			img {
				display: block;
				border-radius: 8px; }

			.detail-preview {
				text-align: center;
				margin-top: 15px; } }


		.info-item {
			padding-top: 0 !important; } } }


.info-item {
	img {
		border-radius: 8px; } }

.row-slider {
	display: flex;
	align-items: center;
	margin-bottom: 40px;



	&__slider {
		width: 50%;
		flex: none;
		margin-left: 30px;

		img {
			display: block;
			border-radius: 10px; }


		.slick-next:before, .slick-prev:before {
			color: #000; } }

	&:nth-child(odd) {
		flex-direction: row-reverse;

		.row-slider__slider {
			margin: 0 40px 0 0; } }


	.b-button {
		height: 53px;
		line-height: 51px;
		padding: 0 46px; }

	.button-detail {
		display: flex;
		justify-content: center; }

	&.not-slider {
		justify-content: center; } }







.travel-list {
	.b-icon {
		flex: none; }

	.service-item {
		display: flex !important;
		justify-content: flex-start; } }

.service-list {
	.b-icon {
		// display: block
		@include size(35px);

		&:before {
			display: none; } } }


.b-service:not(.service-list) {
	.service-item {
		margin-right: 20px; }

	.b-icon {
		display: block;
		@include size(24px);

		&:before {
			display: none; } } }

